.root {
  margin: 65px 150px auto 10px;
}

.userDate {
  display: flex;
  align-items: center;
}

.balance {
  margin-right: 50px;
}

.balance input {
  width: 200px;
  height: 27px;
  border-radius: 7px;
  border: 1px solid #999999;
  padding-left: 5px;
}

.textBalance {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  display: flex;
  align-items: center;
  color: #999999;
}
