.text {
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 20px;
  display: flex;
  align-items: center;
  color: #000000;
  margin: auto auto 10px 20px;
  opacity: 0.8;
}

.root hr {
  border: 1px solid rgba(84, 120, 43, 0.15);
  box-shadow: 0px -3px 24px rgba(84, 120, 43, 0.5);
  transform: rotate(-0.04deg);
}
